
export default {
	async onLaunch() {

		const [, { code }] = await uni.login() // 获取 code
		uniCloud.callFunction({ name: 'login', data: { code } }).then(({ result }) => { // 获用户信息
			this.$store.commit('userinfo/getUserinfo', result)
		})

	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
		uni.hideLoading()
	}
}
